#homebg {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.container {
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin:0;
    left: 0px;
    top: 0px;
    z-index: -1000;
    overflow:hidden;
    background-color: #7ad7ff;
    -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 2s; /* Firefox < 16 */
    -ms-animation: fadein 2s; /* Internet Explorer */
    -o-animation: fadein 2s; /* Opera < 12.1 */
    animation: fadein 2s;
}

.overlay {
    position: absolute;
    top: 30%; 
    left: 20%;
    z-index: 1;
}

#welcome {
    color: green;
    font-size: 20px;
    align-self: center;
}

#home {
    position: relative;
    top: 4%; 
    left: 0%;
    width: 64px;
    height: 64px;
    z-index: 1000;
}

#github {
    position: relative;
    top: 2%; 
    left: 0.3%;
    width: 58px;
    height: 52px;
    z-index: 1000;
}